.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.wrapper {
  max-width: 1278px;
  margin: 0 auto; }

.main-background {
  background-image: url("../img/body-background-main.png");
  background-size: 1366px auto; }

