@media (max-width: 1023px) {
  .page-header {
    justify-content: space-between;
    padding-left: 0;
    padding-right: 0; }
  .page-header__navigation {
    width: 100%;
    margin-right: 0; }
  .page-header__toggle {
    position: relative;
    display: block;
    padding-right: 10px;
    width: 40px;
    height: 40px;
    border: none;
    background-color: transparent;
    cursor: pointer; }
    .page-header__toggle::before {
      content: "";
      position: absolute;
      width: 40px;
      height: 40px;
      top: 0;
      left: 0;
      background-image: url("img/open-button.svg");
      background-repeat: no-repeat;
      background-size: 29px 20px;
      background-position: 0 0; }
  .page-header__toggle--close {
    background-image: url("img/open-button.svg");
    background-repeat: no-repeat;
    background-size: 29px 20px; }
  .page-header__toggle--open {
    background-image: url("img/close-button.svg");
    background-repeat: no-repeat;
    background-size: 19px 19px; }
  .page-header__button {
    order: -1; }
  .page-header__logo {
    order: -1;
    margin: 0;
    width: 28px;
    padding-left: 20px; }
  .page-header__contacts {
    margin-left: 0;
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 30px;
    padding-bottom: 28px;
    flex-direction: column;
    align-items: flex-start;
    border-top: 1px solid #ffffff; } }
  @media (max-width: 1023px) and (max-width: 1023px) {
    .page-header__contacts {
      padding-left: 16px;
      padding-right: 16px; } }

@media (max-width: 1023px) {
  .page-header__list {
    flex-direction: column;
    margin-top: 33px;
    margin-bottom: 32px; }
  .page-header__item {
    padding: 0 8px;
    padding-top: 17px;
    padding-bottom: 18px; }
    .page-header__item::before {
      display: none; }
  .page-header--open {
